import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";
import {
  ICourseObjectiveAddEditVM,
  ICourseObjectiveGetModel,
  ISearchCourse,
  ISearchDiploma,
  IUnitAttachmentAddEditVM,
  IUnitAttachmentResponseVM,
  IUnitResponseVM,
} from "app/admin/models/admin/setup/course";
import { IUnitAttachmentGetVM } from "app/admin/models/admin/setup/course";

@Injectable({
  providedIn: "root",
})
export class CourseService {
  constructor(private http: HttpClient) {}

  ////// main course > diploma
  getAllDiplomasBySearch<T>(data: ISearchDiploma) {
    return this.http.post<T>(`${environment.apiURL}Course/SearchDiploma`, data);
  }

  // getAllDiplomas<T>(page: number) {
  //   return this.http.get<T>(
  //     `${environment.apiURL}Course/geAllCourses/${page}`
  //   );
  // }

  getDiplomaById<T>(id: number) {
    return this.http.get<T>(`${environment.apiURL}Course/getCourseById/${id}`);
  }

  addDiploma<T, TV>(course: T) {
    return this.http.post<TV>(`${environment.apiURL}Course/PostCourse`, course);
  }

  updateDiploma<T, TV>(id: number, course: T) {
    return this.http.put<TV>(
      `${environment.apiURL}Course/PutCourses/${id}`,
      course
    );
  }

  removeDiploma(id: number) {
    return this.http.delete(`${environment.apiURL}Course/DeleteCourses/${id}`);
  }

  getAllDiplomasDropDown<T>(eduCompId: number) {
    return this.http.get<T>(
      `${environment.apiURL}Course/geCourses/${eduCompId}`
    );
  }

  ////// sub course > course
  getAllCoursesBySearch<T>(data: ISearchCourse) {
    return this.http.post<T>(
      `${environment.apiURL}SubCourse/searchSubCourse`,
      data
    );
  }

  // getAllCourses<T>(page: number) {
  //   return this.http.get<T>(
  //     `${environment.apiURL}SubCourse/getAllSubCourse/${page}`
  //   );
  // }

  addCourse<T, TV>(course: T) {
    return this.http.post<TV>(
      `${environment.apiURL}SubCourse/PostSubCourse`,
      course
    );
  }

  getCourseById<T>(id: number) {
    return this.http.get<T>(
      `${environment.apiURL}SubCourse/getSubCourseById/${id}`
    );
  }

  updateCourse<T, TV>(id: number, course: T) {
    return this.http.put<TV>(
      `${environment.apiURL}SubCourse/PutSubCourse/${id}`,
      course
    );
  }

  removeCourse(id: number) {
    return this.http.delete(
      `${environment.apiURL}SubCourse/DeleteSubCourses/${id}`
    );
  }

  updateJobTitleCourse<T, TV>(id: number, dates: T) {
    return this.http.put<TV>(
      `${environment.apiURL}SubCourse/EditSubCourseDepJob/${id}`,
      dates
    );
  }

  removeJobTitleCourse(id: number) {
    return this.http.delete(
      `${environment.apiURL}SubCourse/DeleteJobTitleOfSubCourse/${id}`
    );
  }

  // Start add users to course
  getAllUsers<T>(page: number, subCourseID: number) {
    return this.http.get<T>(
      `${environment.apiURL}UserSubject/geAllUserBySubId/${page}/${subCourseID}`
    );
  }

  checkUser<T, TV>(body: T, eduCompId: number) {
    // console.log(body, eduCompId)
    return this.http.post<TV>(
      `${environment.apiURL}UserSubject/getUserSubjectByEduCompId/${eduCompId}`,
      body
    );
  }

  addUser<T, TV>(obj: T) {
    return this.http.post<TV>(
      `${environment.apiURL}UserSubject/addUserSubject`,
      obj
    );
  }

  deleteUser(subUserID: number) {
    return this.http.delete(
      `${environment.apiURL}UserSubject/DeleteUserSubject/${subUserID}`
    );
  }

  uploadUsersByExcel<T>(obj: T) {
    return this.http.post(
      `${environment.apiURL}UserSubject/addMultiUserSubject`,
      obj
    );
  }

  getMyCourses<T>() {
    return this.http.get<T>(
      environment.apiURL + "UserSubject/getAllUserCourses"
    );
  }

  getBasicAndRecommendedCourses() {
    return this.http.get(
      environment.apiURL + "UserSubject/getBasicRecommendedCourses"
    );
  }

  //#region ///////////////// course structure
  getAllUnitData(unitId: number) {
    return this.http.get<IUnitResponseVM>(
      `${environment.apiURL}Admin/GetAllUnitDate/${unitId}`
    );
  }

  getUnitAttachmentById(attachId: number) {
    return this.http.get<{ fileEduUnit: IUnitAttachmentGetVM[] }>(
      `${environment.apiURL}FileEducationalUnitController/GetFileEduUnitById/${attachId}`
    );
  }

  addEditUnitAttachment(data: IUnitAttachmentAddEditVM) {
    return this.http.post(
      `${environment.apiURL}FileEducationalUnitController/AddEditFileEduUnit`,
      data
    );
  }

  deleteUnitAttachment(attachId: number) {
    return this.http.delete(
      `${environment.apiURL}FileEducationalUnitController/deleteFileEduUnit/${attachId}`
    );
  }

  getAllUnitAttachments(courseId: number) {
    return this.http.get<{ UnitData: IUnitAttachmentResponseVM[] }>(
      `${environment.apiURL}Admin/GetAllUnitFile/${courseId}`
    );
  }
  //#endregion

  //#region ///////////////// course objectives
  getAllCourseObjectives(courseId: number, page: number) {
    return this.http.get<ICourseObjectiveGetModel>(
      `${environment.apiURL}CourseObjective/getAllCourseObjectiveById/${courseId}/${page}`
    );
  }

  addEditCourseObjective(data: ICourseObjectiveAddEditVM) {
    return this.http.post(
      `${environment.apiURL}CourseObjective/AddEditCourseObjective`,
      data
    );
  }

  deleteCourseObjective(objective: number) {
    return this.http.delete(
      `${environment.apiURL}CourseObjective/DeleteCourseObjective/${objective}`
    );
  }
  //#endregion
}
